.game {
    text-align: center;
    font-family: Arial, sans-serif;
  }

  .whack-a-mole {
    text-align: center;
    padding: 0px;
    background-image: url("whack_bg.jpg"); /* 替換成你的圖片路徑 */
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-gap: 5px;
    margin: 20px auto;
    width: 310px;
  }
  
  .Wackcell {
    width: 100px;
    height: 100px;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    border: 2px solid #333;
    cursor: pointer;
    background-color: rgb(184, 184, 184);
    transition: background 0.3s;
  }
  
  .Wackcell:hover {
    background-color: #f0f0f0;
  }
  
  .status {
    font-size: 24px;
    margin: 10px 0;
  }
  
  .reset-btn {
    background-color: #007bff;
    color: white;
    font-size: 18px;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
  }
  
  .reset-btn:hover {
    background-color: #0056b3;
  }
  